import Leaflet from 'leaflet'
import 'leaflet.markercluster'
import Layer from './mixins/Layer'

// marker image fix
// https://github.com/PaulLeCam/react-leaflet/issues/255#issuecomment-261904061
delete Leaflet.Icon.Default.prototype._getIconUrl
Leaflet.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
})

export default {
  name: 'markers',
  mixins: [ Layer ],

  props: {
    positions: {
      type: Array,
      required: true
    },
    addPositions: {
      type: Array
    },
    cluster: {
      type: Boolean,
      default: false
    },
    icon: {
      type: Object
    },
    clickable: {
      type: Boolean,
      default: true
    }
  },

  watch: {
    positions (position) {
      if (this.addPositions.length) {
        this.drawMarkers(this.addPositions)
      } else {
        this.$markercluster.clearLayers()
        this.drawMarkers(this.positions)
      }
    },
    popup (content) {
      // this.$markercluster.setPopupContent(content)
    }
  },

  methods: {
    drawMarkers (positions) {
      console.time('createMarkers')
      positions.forEach(position => {
        if (!position.icon) position.icon = this.icon
        var icon = this.createIcon(position.icon.default)
        let marker = Leaflet.marker(position, { icon: icon })
        marker.bindPopup('', {className: 'xa-hide'})
        marker.data = position
        this.$markercluster.addLayer(marker)
      })
      console.timeEnd('createMarkers')
    },
    setMarkerIcon (marker, active) {
      marker.setIcon(this.createIcon(active ? marker.data.icon.active : marker.data.icon.default))
    },
    onMarkerRemove (ev) {
      this.$emit('blur')
      ev.target.off('remove', this.onMarkerRemove)
    },
    onMarkerAdd (ev) {
      this.setMarkerIcon(ev.target)
      ev.target.off('add', this.onMarkerAdd)
    },
    createIcon (iconOption) {
      var size = iconOption.size || 30
      return Leaflet.divIcon({
        className: 'my-div-icon',
        iconSize: [size, size],
        iconAnchor: iconOption.anchor || null,
        html: `<i class="iconfont ${iconOption.iconfont}" style="display:block;color:${iconOption.color};height:${size}px;width:${size}px;line-height:${size}px;font-size:${size}px;"></i>`
      })
    }
  },
  created () {
    this.$markercluster = !this.cluster
    ? Leaflet.featureGroup()
    : Leaflet.markerClusterGroup({
      iconCreateFunction: (cluster) => {
        var childCount = cluster.getChildCount()
        var c = ' marker-cluster-'
        if (childCount < 10) {
          c += 'small'
        } else if (childCount < 100) {
          c += 'medium'
        } else {
          c += 'large'
        }

        return new Leaflet.DivIcon({ html: '<div><span>' + childCount + '</span></div>', className: 'marker-cluster' + c, iconSize: new Leaflet.Point(40, 40) })
      }
    })
    this.drawMarkers(this.positions)
    this.$setLayer(this.$markercluster)
  },

  mounted () {
    if (!this.clickable) return
    const mapEvents = [
      'click', 'popupopen', 'popupclose'
    ]
    mapEvents.forEach(eventName => this.$markercluster.on(eventName, ev => {
      if (ev.layer.data.icon.active) {
        if (eventName === 'popupopen') {
          ev.layer.on('remove', this.onMarkerRemove).on('add', this.onMarkerAdd)
          this.setMarkerIcon(ev.layer, true)
        } else if (eventName === 'popupclose') {
          ev.layer.off('remove', this.onMarkerRemove)
          this.setMarkerIcon(ev.layer)
        }
      }
      var event = eventName === 'popupopen' && 'focus' || eventName === 'popupclose' && 'blur' || 'click'
      this.$emit(event, ev.layer.data)
    }))
  }
}



