import Leaflet from 'leaflet'
import Layer from './mixins/Layer'

export default {

  mixins: [ Layer ],

  props: {
    coordinates: {
      type: Array,
      required: true
    },
    style: {
      type: Object,
      default () {
        return { color: '#6f0', opacity: 1, fillOpacity: 0.5, weight: 1 }
      }
    }
  },

  created () {
    this.$polylines = Leaflet.featureGroup()
    this.coordinates.forEach(item => {
      if (!item.style) item.style = this.style
      this.$polylines.addLayer(Leaflet.polyline(item, item.style))
    })
    this.$setLayer(this.$polylines)
  }
}



