import Leaflet from 'leaflet'
import Layer from './mixins/Layer'

export default {
  mixins: [ Layer ],
  props: {
    coordinates: {
      type: Array,
      required: true
    },
    cluster: {
      type: Boolean,
      default: false
    },
    clusterZoom: {
      type: Number,
      default: 15
    },
    barriers: {
      type: Boolean,
      default: true
    },
    barriersStyle: {
      type: Object,
      default () {
        return {color: 'red', opacity: 1, fillOpacity: 0.5, weight: 1, clickable: false}
      }
    },
    airlines: {
      type: Boolean,
      default: true
    },
    airlinesStyle: {
      type: Object,
      default () {
        return {color: 'orange', opacity: 1, weight: 1, clickable: false}
      }
    },
    style: {
      type: Object,
      default () {
        return {
          default: { color: 'rgb(158, 165, 148)', opacity: 1, fillOpacity: 0.5, weight: 1 },
          active: { color: '#6f0', opacity: 1, fillOpacity: 0.5, weight: 1 }
        }
      }
    }
  },
  methods: {
    drawMarkers (coordinates) {
      console.time('createMarkers')
      coordinates.forEach(item => {
        let mk = Leaflet.marker(item.polygonCenter)
        mk.data = item
        this.$markercluster.addLayer(mk)
      })
      console.timeEnd('createMarkers')
    },
    onZoomEnd (container) {
      let zoom = container.getZoom()
      if (zoom >= this.clusterZoom) {
        this.$setLayer(this.$polygons)
        console.log(this.$polygons.toGeoJSON())
        //   
        console.log(container.hasLayer(this.$barriers))
        if (this.barriers) this.$barriers.addTo(container)
        if (this.airlines) this.$airlines.addTo(container)
      } else {
        this.$setLayer(this.$markercluster)
        if (this.barriers) this.$barriers.remove()
        if (this.airlines) this.$airlines.remove()
      }
    }
  },
  created () {
    this.$polygons = Leaflet.featureGroup()
    this.$barriers = Leaflet.featureGroup()
    this.$airlines = Leaflet.featureGroup()
    this.$polygons.bindPopup('', {className: 'xa-hide'}).on('popupopen', (e) => {
      e.layer.setStyle(e.layer.data.style.active)
      this.$emit('focus')
    }).on('popupclose', (e) => {
      e.layer.setStyle(e.layer.data.style.default)
      this.$emit('blur')
    })
    console.time('createPolygons')
    this.coordinates.forEach(item => {
      if (!item.style) item.style = JSON.parse(JSON.stringify(this.style))
      let polygon = Leaflet.polygon(item.bounds, item.style.default)
      polygon.data = item
      polygon.data.polygonCenter = polygon.getBounds().getCenter()
      this.$polygons.addLayer(polygon)
      if (item.barriers && item.barriers.length) {
        item.barriers.forEach(it => {
          this.$barriers.addLayer(Leaflet.polygon(it, this.barriersStyle))
        })
      }
      if (item.airlines && item.airlines.length) {
        item.airlines.forEach(it => {
          this.$airlines.addLayer(Leaflet.polyline(it, this.airlinesStyle))
        })
      }
    })
    console.timeEnd('createPolygons')
    // 
    if (this.cluster) {
      this.$markercluster = Leaflet.markerClusterGroup({
        singleMarkerMode: true,
        showCoverageOnHover: false,
        iconCreateFunction: (cluster) => {
          var childCount = cluster.getChildCount()
          var c = ' land-cluster-'
          if (childCount <= 1) {
            c += 'one'
          } else if (childCount < 10) {
            c += 'small'
          } else if (childCount < 100) {
            c += 'medium'
          } else {
            c += 'large'
          }
          return new Leaflet.DivIcon({
            html: `
              <div class="land-bg">
                <i class="iconfont icon-shixianliubianxing"></i>
              </div>
              <div class="land-count">
                <span>${childCount}</span>
              </div>`,
            className: 'land-cluster' + c,
            iconSize: new Leaflet.Point(40, 40)
          })
        }
      }).on('click', (e) => {
        this.$parent.$container.setView(e.latlng, this.clusterZoom)
      })
      this.drawMarkers(this.coordinates)
      this.$parent.$containerReady.then(container => {
        container.on('zoomend', e => {
          this.onZoomEnd(e.target)
        })
        this.onZoomEnd(container)
      })
    } else {
      this.$setLayer(this.$polygons)
    }
  },
  mounted () {
    // const mapEvents = [
    //   'click'
    // ]
    // mapEvents.forEach(eventName => this.$polygons.on(eventName, ev => this.$emit(eventName, ev.layer.data)))
  }

}



