<template>
  <div id="app">
    <vmap style="height:100%;" :position="position" :zoom="zoom">
      <vmap-tile-layer url="http://{s}.tiles.mapbox.com/v3/spatialdev.map-c9z2cyef/{z}/{x}/{y}.png"></vmap-tile-layer>
      <vmap-polygons @click="onPolygonClick" :coordinates="coordinates" cluster></vmap-polygons>
    </vmap>
  </div>
</template>

<script>
import * as components from '../../src/components'

export default {
  components: {
    Vmap: components.Map,
    VmapTileLayer: components.TileLayer,
    VmapPolygons: components.Polygons
  },
  data () {
    return {
      zoom: 5,
      style: {
        default: { color: 'yellow', opacity: 1, fillOpacity: 0, weight: 3 },
        active: { color: '#6f0', opacity: 1, fillOpacity: 0, weight: 3 }
      },
      coordinates: Array.apply(null, { length: 50 }).map(() => {
        var xlat = 15 + Math.random() * 30
        var xlng = 80 + Math.random() * 44
        return {
          bounds: Array.apply(null, { length: 5 }).map(() => ({ lat: xlat + Math.random() * 0.005, lng: xlng + Math.random() * 0.005 })),
          barriers: [ Array.apply(null, { length: 3 }).map(() => ({ lat: xlat + Math.random() * 0.001 + 0.0025, lng: xlng + Math.random() * 0.001 + 0.0025 })) ],
          airlines: [
            Array.apply(null, { length: 4 }).map(() => ({ lat: xlat + Math.random() * 0.001 + 0.0025, lng: xlng + Math.random() * 0.001 + 0.0025 })),
            Array.apply(null, { length: 4 }).map(() => ({ lat: xlat + Math.random() * 0.001 + 0.0025, lng: xlng + Math.random() * 0.001 + 0.0025 }))
          ]
        }
      })
    }
  },
  computed: {
    position () {
      return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44 }
    }
  },
  methods: {
    onPolygonClick (e) {
      console.log(e)
    }
  },
  mounted () {
  }
}
</script>



