<template>
  <div id="app">
    <vmap style="height:100%;" :position="position" :zoom="zoom">
      <vmap-tile-layer url="http://{s}.tiles.mapbox.com/v3/spatialdev.map-c9z2cyef/{z}/{x}/{y}.png"></vmap-tile-layer>
      <vmap-markers :positions="markers1" :icon="renIcon" popup="ren"></vmap-markers>
      <vmap-markers :positions="markers2" cluster popup="" @focus="onFocus" @blur="onBlur"></vmap-markers>
      <vmap-markers :positions="markers"></vmap-markers>
    </vmap>
  </div>
</template>

<script>
import * as components from '../../src/components'

export default {
  components: {
    Vmap: components.Map,
    VmapMarker: components.Marker,
    VmapMarkers: components.Markers,
    VmapGeoJson: components.GeoJson,
    VmapCircle: components.Circle,
    VmapLayerGroup: components.LayerGroup,
    VmapPolyline: components.Polyline,
    VmapRectangle: components.Rectangle,
    VmapTileLayer: components.TileLayer,
    VmapPolygon: components.Polygon
  },
  data () {
    return {
      zoom: 5,
      markers: [
        { lat: 23.2123, lng: 113.234234, icon: { default: { iconfont: 'icon-dingwei', anchor: [ 15, 30 ] }, active: { iconfont: 'icon-location', anchor: [ 15, 30 ] } } }
      ],
      markers1: Array.apply(null, { length: 50 }).map(() => {
        return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44 }
      }),
      renIcon: {default: { iconfont: 'icon-ren', color: 'green', size: 30 }, active: { iconfont: 'icon-ren', color: 'orange', size: 40 }},
      markers2: Array.apply(null, { length: 4000 }).map(() => {
        return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44, icon: {default: {iconfont: 'icon-wurenji', color: 'white'}, active: {iconfont: 'icon-wurenji', color: 'red', size: 50}} }
      })
    }
  },
  computed: {
    position () {
      return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44 }
    }
  },
  methods: {
    onBlur (data) {
      console.log('close', data)
    },
    onFocus (data) {
      data.icon = {
        default: { iconfont: 'icon-wurenji', color: 'gray' },
        active: { iconfont: 'icon-wurenji', color: '#369', size: 40 }
      }
      console.log('open', data)
    }
  },
  mounted () {
    // setTimeout(() => {
    //   this.markers1 = Array.apply(null, { length: 10 }).map(() => {
    //     return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44 }
    //   })
    //   this.markers2 = Array.apply(null, { length: 10 }).map(() => {
    //     return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44 }
    //   })
    // }, 3000)
  }
}
</script>



