// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueRouter from 'vue-router'
import App from './App'

Vue.use(VueRouter)

const routes = [
  { path: '/index', alias: '/', component: require('./views/index.vue') },
  { path: '/markers', component: require('./views/markers.vue') },
  { path: '/polygons', component: require('./views/polygons.vue') },
  { path: '/polylines', component: require('./views/polylines.vue') }
  // { path: '/area', component: require('./views/area.vue') }
]

const router = new VueRouter({
  routes
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  template: '<App/>',
  components: { App }
})



