export default {
  template: '<div v-if="isNeedDom"><slot></slot></div>',
  computed: {
    isNeedDom () {
      return Object.keys(this.$slots).length
    }
  },
  beforeCreate () {
    if (!this.$parent.$containerReady) {
      throw new Error('A Layer component must be a child of LayerContainer')
    }
    this.$removeLayer = () => {
      if (this.$layer) {
        this.$parent.$container.removeLayer(this.$layer)
      }
    }
    this.$setLayer = layer => {
      if (this.$layer === layer) return
      this.$removeLayer()
      this.$layer = layer
      this.$parent.$containerReady.then(container => {
        container.addLayer(layer)
      })
    }
  },
  beforeDestroy () {
    this.$removeLayer()
  }

}



