import Leaflet from 'leaflet'
import Layer from './mixins/Layer'

// marker image fix
// https://github.com/PaulLeCam/react-leaflet/issues/255#issuecomment-261904061
delete Leaflet.Icon.Default.prototype._getIconUrl
Leaflet.Icon.Default.mergeOptions({
  iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
  iconUrl: require('leaflet/dist/images/marker-icon.png'),
  shadowUrl: require('leaflet/dist/images/marker-shadow.png')
})

export default {
  mixins: [ Layer ],

  props: {
    position: {
      type: Object,
      required: true
    },
    icon: {
      type: Object,
      default () {
        return {iconfont: 'icon-location', color: 'white'}
      }
    },
    popup: {
      type: String,
      default: ''
    },
    draggable: {
      type: Boolean,
      default: false
    },
    index: {
      type: Number
    }
  },

  watch: {
    position: {
      handler (position) {
        this.$marker.setLatLng(position)
      },
      deep: true
    },
    icon: {
      handler () {
        this.setMarkerIcon()
      },
      deep: true
    },
    draggable (draggable) {
      if (!this.$marker.dragging) return
      draggable
      ? this.$marker.dragging.enable()
      : this.$marker.dragging.disable()
    },
    popup (content) {
      this.$marker.setPopupContent(content)
    }
  },
  methods: {
    setMarkerIcon () {
      this.$marker.setIcon(this.createIcon(this.icon))
    },
    createIcon (iconOption) {
      var size = iconOption.size || 30
      return Leaflet.divIcon({
        className: 'my-div-icon',
        iconSize: [size, size],
        iconAnchor: iconOption.anchor || null,
        html: `<i class="iconfont ${iconOption.iconfont}" style="display:block;color:${iconOption.color};height:${size}px;width:${size}px;line-height:${size}px;font-size:${size}px;"></i>`
      })
    }
  },
  created () {
    this.$marker = Leaflet.marker(this.position, {draggable: this.draggable})
    this.setMarkerIcon()
    this.$marker.data = this.position
    this.$marker.icon = this.icon
    if (this.index) this.$marker.index = this.index
    this.$marker.bindPopup(this.popup, this.popup ? {} : {className: 'xa-hide'})
    this.$setLayer(this.$marker)
  },

  mounted () {
    // passthru events
    const mapEvents = [
      'click', 'dblclick', 'mousedown', 'mouseover', 'mouseout', 'contextmenu', 'dragstart', 'drag', 'dragend',
      'move', 'add', 'remove', 'popupopen', 'popupclose'
    ]
    mapEvents.forEach(eventName => this.$marker.on(eventName, ev => {
      if (eventName === 'popupopen') this.$emit('focus', ev)
      if (eventName === 'popupclose') this.$emit('blur', ev)
      this.$emit(eventName, ev)
    }))
  }
}



