<template>
  <div id="app">
    <vmap style="height:100%;" @click="onMapClick" :position="position" :zoom="zoom" @zoom="onZoom" @move="onMove">
      <vmap-tile-layer></vmap-tile-layer>
      <!--<vmap-markers @click="onMarkersClick" :positions="markers" cluster></vmap-markers>-->
      <vmap-polygons @click="onPolygonClick" :coordinates="coordinates" cluster></vmap-polygons>
      <vmap-marker-cluster-group>
        <vmap-marker
          v-for="(pos,ix) in singleMarkers"
          @focus="onMarkerFocus"
          @blur="onMarkerBlur"
          :position="pos"
          :icon="pos.icon"
          :index="ix">
        </vmap-marker>
      </vmap-marker-cluster-group>
    </vmap>
    <div style="position:fixed;z-index:500;width:200px;top:20px;right:20px;padding:20px;background:rgba(0,0,0,0.6);color:white;">
      <input type="number" v-model="currentMarker.icon.size" style="width:50px;" step="2"><br>
      <input type="color" v-model="currentMarker.icon.color" style="width:50px;"><br>
      <input type="number" v-model="currentMarker.position.lat" step="0.001" style="width:120px;"><br>
      <input type="number" v-model="currentMarker.position.lng" step="0.001" style="width:120px;"><br>
      <label v-for="icon in iconfonts">
            <input type="radio" v-model="currentMarker.icon.iconfont" name="iconfont" v-show="false" :value="icon" >
            <i class="iconfont" :class="[icon]" :style="{'font-size':'24px','color':currentMarker.icon.iconfont == icon ? currentMarker.icon.color : 'white'}"></i>
          </label>
      <button @click="removeMarker"></button>
      <button @click="addMarker"></button>
    </div>
  </div>
</template>

<script>
import * as components from '../../src/components'

export default {
  components: {
    Vmap: components.Map,
    VmapMarkers: components.Markers,
    VmapMarker: components.Marker,
    VmapTileLayer: components.TileLayer,
    VmapLayerGroup: components.LayerGroup,
    VmapMarkerClusterGroup: components.MarkerClusterGroup,
    VmapPolygons: components.Polygons
  },
  data () {
    return {
      zoom: 5,
      iconfonts: [
        'icon-wurenji',
        'icon-cheliang',
        'icon-icon4',
        'icon-nongye',
        'icon-tuandui',
        'icon-ren'
      ],
      singleMarkers: Array.apply(null, { length: 200 }).map(() => {
        return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44, icon: {iconfont: 'icon-wurenji', color: '#ffffff', size: 30} }
      }),
      markers: Array.apply(null, { length: 200 }).map(() => {
        return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44, icon: {default: {iconfont: 'icon-wurenji', color: '#ffffff', size: 30}, active: {iconfont: 'icon-wurenji', color: 'red', size: 50}} }
      }),
      currentMarker: {
        position: { lat: 0, lng: 0 },
        icon: { color: '#ffffff', size: 30, iconfont: 'icon-wurenji' },
        index: undefined
      },
      markerGeoJson: {
        type: 'MultiPoint',
        coordinates: [ [113.0, 23.0], [113.0, 24.0] ]
      },
      coordinates: Array.apply(null, { length: 50 }).map(() => {
        var xlat = 15 + Math.random() * 30
        var xlng = 80 + Math.random() * 44
        return {
          center: { lat: xlat + Math.random() * 0.005, lng: xlng + Math.random() * 0.005 },
          bounds: [ { lat: xlat - 0.002, lng: xlng + 0.0025 }, { lat: xlat - 0.003, lng: xlng }, { lat: xlat - 0.002, lng: xlng - 0.0025 }, { lat: xlat + 0.002, lng: xlng - 0.0025 }, { lat: xlat + 0.003, lng: xlng }, { lat: xlat + 0.002, lng: xlng + 0.0025 } ],
          barriers: [ Array.apply(null, { length: 3 }).map(() => ({ lat: xlat + Math.random() * 0.003 - 0.0025, lng: xlng + Math.random() * 0.003 - 0.0025 })) ],
          airlines: [
            Array.apply(null, { length: 4 }).map(() => ({ lat: xlat + Math.random() * 0.001 + 0.0025, lng: xlng + Math.random() * 0.001 + 0.0025 })),
            Array.apply(null, { length: 4 }).map(() => ({ lat: xlat + Math.random() * 0.001 + 0.0025, lng: xlng + Math.random() * 0.001 + 0.0025 }))
          ]
        }
      })
    }
  },
  computed: {
    position () {
      return { lat: 15 + Math.random() * 30, lng: 80 + Math.random() * 44 }
    }
  },
  methods: {
    onMapClick (ev) {
      console.log('mapClick')
      this.currentMarker.position = ev.latlng
    },
    onZoom (data) {
      this.zoom = data.zoom
    },
    onPolygonClick (e) {
      console.log(e)
    },
    onMove (data) {
      this.lat = data.position.lat
      this.lng = data.position.lng
    },

    removeMarker () {
      var index = this.currentMarker.index
      console.log(index)
      if (index !== undefined) this.singleMarkers.splice(index, 1)
    },

    addMarker () {
      this.singleMarkers.push({
        ...this.currentMarker.position,
        icon: { ...this.currentMarker.icon }
      })
    },

    onMarkerFocus (e) {
      this.currentMarker.position = e.target.data
      this.currentMarker.icon = e.target.icon
      this.currentMarker.index = e.target.index
    },

    onMarkerBlur (e) {
      this.currentMarker.position = { lat: 0, lng: 0 }
      this.currentMarker.icon = { color: '#ffffff', size: 30, iconfont: 'icon-wurenji' }
      this.currentMarker.index = undefined
    },
    onMarkersClick (item) {
      console.log(item)
    },
    onPopupOpen () {
      console.log(1)
    }
  },
  mounted () {
    var a = { x: 1, y: 2 }
    var b = { y: 3, z: 4 }
    console.log({ ...a, ...b })
  }
}
</script>



